

.btn {
	letter-spacing: $btn-letter-spacing;
}

.btn-md {
	@include button-size($btn-padding-y, $btn-padding-x, $btn-font-size, $btn-line-height, $btn-border-radius);
}

.btn-has-arrow-icon {
	position: relative;
	@include hover() {
		.bi-arrow-right {
			right: -.25em;
		}
		.bi-arrow-right-long {
			right: -3em;
		}
	}
	.bi-arrow-right {
		vertical-align: middle;
		display: inline-block;
		position: relative;
		margin-left: .5em;
		margin-right: -.5em;
		top: -.08em;
		right: 0;
		transition: right ease-out .15s;
	}
	.bi-arrow-right-long {
		position: absolute;
		top: 50%;
		width: 2.5em;
		height: 1.666666667em;
		margin-top: (1.666666667em / -2);
		right: -1.5em;
		transition: right ease-out .15s;
	}
}

.btn-link {
	padding: 0;
	padding-bottom: .3em;
	font-weight: $btn-font-weight;
	outline: none;
	border-width: 0;
	border-bottom-width: 3px;
	&:focus {
		box-shadow: none;
	}
	&.btn-lg {
		border-bottom-width: 4px;
	}
	&.btn-sm {
		border-bottom-width: 2px;
	}
	&.btn-has-arrow-icon {
		.bi-arrow-right {
			margin-right: -1.5em;
		}
	}
	@each $color, $value in $theme-colors {
		&.btn-link-#{$color} {
			color: $value;
			border-color: $value;
			&:hover {
				color: darken($value, 15%);
				border-color: darken($value, 15%);
			}
		}
	}
}

@each $color, $value in $theme-colors {
	.btn-outline-#{$color} {
		border-width: 3px;
		padding: calc(#{$btn-padding-y} - (3px - #{$btn-border-width})) calc(#{$btn-padding-x} - (3px - #{$btn-border-width}));
		@include hover() {
			// color: $value;
			// background-color: fade-out($value, 0.9);
			// border-color: $active-border;
		}
		&.btn-has-arrow-icon {
			.bi-arrow-right-long {
				fill: $value;
			}
		}
		&.btn-lg {
			border-width: 4px;
			padding: calc(#{$btn-padding-y-lg} - (4px - #{$btn-border-width})) calc(#{$btn-padding-x-lg} - (4px - #{$btn-border-width}));
		}
		&.btn-sm {
			border-width: 2px;
			padding: calc(#{$btn-padding-y-sm} - (2px - #{$btn-border-width})) calc(#{$btn-padding-x-sm} - (2px - #{$btn-border-width}));
		}
	}
}

@each $breakpoint, $width in $grid-breakpoints {
	.btn-block-to-#{$breakpoint} {
		display: block;
		width: 100%;
		@include media-breakpoint-up($breakpoint) {
			display: inline-block;
			width: auto;
			+ .btn-block {
				margin-top: 0;
			}
			@each $breakpoint2, $width2 in $grid-breakpoints {
				+ .btn-block-to-#{$breakpoint2} {
					margin-top: 0;
				}
			}
		}
		+ .btn-block {
			margin-top: $btn-block-spacing-y;
		}
		@each $breakpoint2, $width2 in $grid-breakpoints {
			@if $width2 >= $width {
				+ .btn-block-to-#{$breakpoint2} {
					margin-top: $btn-block-spacing-y;
				}
			}
		}
	}
}
.btn-block {
	
	display: block;
	width: 100%;

	// Vertically space out multiple block buttons
	+ .btn-block {
		margin-top: $btn-block-spacing-y;
	}
}


